<template>
  <div class="login">
    <div class="login-bar">
      <van-nav-bar left-text="返回" right-text="关闭" @click-right="toHome">
        <template #left>
          <div class="logo fl">
            <img src="../assets/img/logo1.png" alt />
          </div>
        </template>
      </van-nav-bar>
    </div>
    <div class="login-form">
      <div class="form-title">
        <h3>欢迎回来！</h3>
        <p>
          Please login to your
          accounts
        </p>
      </div>

      <van-form>
        <van-field v-model="loginData.phone" name="手机号" placeholder="手机号" />
        <van-field
          v-model="loginData.password"
          :type="isPasswordShow?'text':'password'"
          name="密码"
          placeholder="密码"
          :right-icon="isPasswordShow?'eye-o':'closed-eye'"
          @click-right-icon="showPassword"
        />
        <div class="btn">
          <div class="forgot fr" @click="forgot">忘记密码？</div>
          <van-button round block @click="login" type="info"  native-type="submit" color="#0b34ba">登录</van-button>
          <div class="signup">
            <span @click="showPopup">注册</span>
          </div>
          <van-popup
            v-model="show"
            round
            closeable
            position="bottom"
            overlay-class="popup-mask"
            :style="{ height: '55%' }"
          >
            <div class="signup-form">
              <h1>注册</h1>
              <van-form>
                <van-field v-model="signupData.phone" type="tel" placeholder="手机号 中国（+86）" />
                <van-field v-model="signupData.nickName" type="text" name="昵称" placeholder="昵称" />
                <van-field
                  v-model="signupData.password"
                  :type="isPasswordShow?'text':'password'"
                  name="密码"
                  placeholder="密码"
                  :right-icon="isPasswordShow?'eye-o':'closed-eye'"
                  @click-right-icon="showPassword"
                />
                <div class="btn">
                  <van-button round block color="#0b34ba" @click="signup">注册</van-button>
                </div>
              </van-form>
            </div>
          </van-popup>
        </div>
      </van-form>
    </div>
  </div>
</template>

<script>
import "../assets/css/login.less";

import { validForm } from "../assets/js/validForm";

import { utils } from "../assets/js/utils";

export default {
  data() {
    return {
      loginData: {
        phone: "",
        password: "",
      },
      signupData: {
        phone: "",
        nickName: "",
        password: "",
      },
      isPasswordShow: false,
      show: false,
    };
  },
  methods: {
    // 去首页
    toHome() {
      this.$router.push({ name: "Home" });
    },

    //查看密码
    showPassword() {
      this.isPasswordShow = !this.isPasswordShow;
    },

    // 弹出注册层
    showPopup() {
      this.show = true;
    },

    // 忘记密码
    forgot(){
      this.$router.push({name:"Forgot"})
    },

    // 注册按钮点击事件
    signup() {
      //获取用户注册信息
      let userInfo = this.signupData;
      //  

      // 验证表单
      let o = {
        phone:{
          value:userInfo.phone,
          reg:/^1[3-9]\d{9}$/,
          errorMsg:"手机号码格式不正确"
        },
        nickName:{
          value:userInfo.nickName,
          reg:/^[\w\u4e00-\u9fa5]{1,10}$/,
          errorMsg:"昵称格式不正确"
        },
        password:{
          value:userInfo.password,
          reg:/^[A-Za-z]\w{5,15}$/,
          errorMsg:"密码格式不正确"
        }
      }

       if (!validForm.valid(o)) {
        //验证不通过
        return;
      }

      // 发起请求
      this.$toast.loading({
        message: "加载中...",
        // 防止点击穿透
        forbidClick: true,
        //显示时间, 如果时间为0，则不会自动关闭
        duration: 0,
        loadingType: "spinner",
      });

      //复制userInfo
      let info = Object.assign({}, userInfo);
      info.appkey = this.appkey

      //参数序列化
      let data = utils.queryString(info);
       
      this.axios({
        method:"POST",
        url:"/register",
        data
      }).then(result=>{
         this.$toast.clear();
         
        // 注册成功
        if(result.data.code==100){
          // 收回注册层
          this.show =false
          //清空注册表单的数据
          for (let key in userInfo) {
            userInfo[key] = ""
          }
        }else {
          //如果注册失败,则提示
          this.$notify({
            type: 'warning', 
            message: result.data.msg
          })
        }
        
      }).catch(err => {
         this.$toast.clear();
         
      })



    },

    login(){
      //获取用户注册信息
      let userInfo = this.loginData;

      // 验证表单
      let o = {
        phone:{
          value:userInfo.phone,
          reg:/^1[3-9]\d{9}$/,
          errorMsg:"手机号码格式不正确"
        },
        password:{
          value:userInfo.password,
          reg:/^[A-Za-z]\w{5,15}$/,
          errorMsg:"密码格式不正确"
        }
      }

       if (!validForm.valid(o)) {
        //验证不通过
        return;
      }

      //验证通过
      this.$toast.loading({
        message: '加载中...',
        forbidClick: true,
        duration: 0,
        loadingType: 'spinner'
      })

      //复制userInfo
      let info = Object.assign({}, userInfo);
      info.appkey = this.appkey;

      //参数序列化
      let data = utils.queryString(info);

      this.axios({
        method: 'POST',
        url: '/login',
        data
      }).then(result => {
        this.$toast.clear();
         
        if (result.data.code == 200) {
          // 存储token
          localStorage.setItem('YXCFTK', result.data.token);
          this.$router.push({name: 'Home'});
        } else {
          this.$notify({
            type: 'warning', 
            message: result.data.msg
          })
        }
      }).catch(err => {
        this.$toast.clear();
         
      })

    }
  },
};
</script>
